.hvr-underline-from-left {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  position: relative;
  overflow: hidden;
  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    left: 0;
    right: 100%;
    bottom: 0;
    background: $primary_color;
    height: 4px;
    -webkit-transition-property: right;
    transition-property: right;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
  }
  &:hover{
    &::before {
      right: 0;
    }
  }
}

.section-heading {
  font-weight: 600;
  font-size: 32px;
  line-height: 39px;
  color: $primary_text_color;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 16px;
  position: relative;
  margin-bottom: 40px;
  &::after,&::before {
    content: "";
    width: 30px;
    height: 40px;
    position: relative;
    top: 5px;
  }
  &::before {
    background: url(/build/img/icon-graphic-left.png) no-repeat;
  }
  &::after {
    background: url(/build/img/icon-graphic.png) no-repeat;
  }
}